export function getArticleLists(classifyId) {
    let list = [];
    if (classifyId === '') {
        list = articleList;
    } else {
        articleList.forEach(item => {
            if (item.classifyId === classifyId) {
                list.push(item);
            }
        })
    }
    return list;
}

