<template>
  <div class="logup" @click="hideLogup">
    <div class="logup-content">
      <div class="logup-logo">
        <img src="~@/icons/common/logo.png" />
      </div>
      <div class="to-login-btn">
        <span @click="toLogin"></span>
      </div>
      <div>
        <form class="logup-form">
          <div class="form-input">
            <input type="number" placeholder="" v-model="phoneNumber" @input="phoneNumLen" />
          </div>
          <div class="form-input">
            <input type="text" placeholder="2-10" v-model="username" />
          </div>
          <div class="form-input">
            <input
              type="password"
              placeholder="6-16"
              maxlength="16"
              minlength="6"
              v-model="password"
            />
          </div>
          <div class="form-input">
            <input
              type="password"
              placeholder=""
              maxlength="16"
              minlength="6"
              v-model="old_password"
            />
          </div>
          <div class="form-input">
            <input type="number" placeholder="" v-model="vercode" />
            <span></span>
          </div>
          <p class="user-pro">
            <input type="checkbox" v-model="check" /> 
            <a href="javascript:0"></a>
          </p>
          <button type="button" class="logup-submit-btn" @click="submitLogup"></button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { post } from '@/network/post';

export default {
  name: 'Logup',
  data() {
    return {
      phoneNumber: '',
      username: '',
      password: '',
      old_password: '',
      check: false,
      vercode: '123456'
    };
  },
  methods: {
    submitLogup() {
      //
      let phoneNumber = this.parityPhoneNumber(this.phoneNumber);
      if (!phoneNumber) return;
      //
      let username = this.parityUserName(this.username);
      if (!username) return;
      //
      let password = this.parityPassword(this.password);
      if (!password) return;
      //
      if (password != this.old_password) {
        this.showPopup(``);
        return;
      }
      //
      //soming code.
      //
      if (!this.check) {
        this.showPopup(``);
        return;
      }
      this.showPopup('')
      //
      // post({
      //   url: 'logup',
      //   method: 'post',
      //   data: {
      //     phoneNumber,
      //     username,
      //     password
      //   }
      // })
      //   .then(res => {
      //     if (res.err == 0) {
      //       this.$store.state.isShowLogup = false;
      //       this.showPopup(res.msg);
      //       //
      //     } else {
      //       this.showPopup(res.msg);
      //     }
      //   })
      //   .catch(function(err) {
      //     console.log(err);
      //   });
    },
    //
    hideLogup(e) {
      let divClass = e.target.className;
      if (divClass === 'logup logup-component') {
        this.$store.state.isShowLogup = false;
      }
    },
    showPopup(str) {
      //
      this.$store.state.isShowPopup = true;
      this.$store.state.toastMsg = str;
    },
    //
    toLogin() {
      this.$store.state.isShowLogin = true;
      this.$store.state.isShowLogup = false;
    },
    //
    phoneNumLen() {
      if (this.phoneNumber.length >= 11) {
        this.phoneNumber = this.phoneNumber.slice(0, 11);
      }
    },
    //
    parityPhoneNumber(value) {
      let Reg = /^(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (value.trim() == '') {
        this.showPopup(``);
        return false;
      } else if (!Reg.test(value)) {
        this.showPopup(``);
        return false;
      }
      return value;
    },
    //
    parityUserName(value) {
      let Reg = /^[\u4e00-\u9fa5a-zA-Z0-9_]{2,10}$/;
      let reg1 = /[^\u4e00-\u9fa5a-zA-Z0-9_]/;
      if (value.trim() == '') {
        this.showPopup(``);
        return false;
      } else if (reg1.test(value)) {
        this.showPopup(`_`);
        return false;
      } else if (!Reg.test(value)) {
        this.showPopup(`2-10`);
        return false;
      }
      return value;
    },
    //
    parityPassword(value) {
      let Reg = /.{6,16}$/;
      if (value == '') {
        this.showPopup(``);
        return false;
      } else if (!Reg.test(value)) {
        this.showPopup(``);
        return false;
      }
      return value;
    }
  }
};
</script>

<style lang='scss' scoped>
@import '~@/styles/variables';
.logup {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.logup-content {
  width: 500px;
  padding: 50px;
  background-color: #fff;
}
.logup-logo {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
}
.to-login-btn {
  color: #4a90e2;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.to-login-btn span {
  cursor: pointer;
  font-size: 16px;
}
.logup-form {
  font-size: 16px;
  .form-input {
    width: 100%;
    height: 58px;
    position: relative;
    border: 1px solid $themeColor;
    margin-top: 20px;
    display: flex;
    align-items: center;
    input {
      width: 100%;
      box-sizing: border-box;
      height: 58px;
      line-height: 58px;
      padding: 0 20px;
    }
    span {
      position: absolute;
      height: 58px;
      cursor: pointer;
      color: #4a90e2;
      line-height: 58px;
      padding-right: 20px;
      font-size: 14px;
      display: block;
      top: 0;
      right: 0;
    }
  }
  .logup-submit-btn {
    width: 100%;
    height: 60px;
    background-color: $themeColor;
    color: #fff;
    cursor: pointer;
  }
  .user-pro {
    padding: 20px 0;
    font-size: 14px;
    a {
      color: #4a90e2;
    }
  }
}

.other-ways {
  margin: 30px 0;
}
</style>